.toggle {
	display: inline-block;
	position: relative;
	width: 60px;
	height: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.toggle input[type="checkbox"] {
	display: none;
}

.toggle label {
	display: block;
	position: relative; 
	left: 0; 
	width: 100%;
	height: 100%;
	border-radius: 15px;
	background-color: #222;
	cursor: pointer;
	transition: all 0.3s ease-in-out; 
}
.toggle label:after {
	content: "";
	display: block;
	position: absolute;
	top: 2px;
	left: 2px;
	width: 26px;
	height: 26px;
	border-radius: 50%;
	background-color: #fff;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.toggle input[type="checkbox"]:checked + label {
	background-color: #007bff;
}

.toggle input[type="checkbox"]:checked + label:after {
	transform: translateX(30px);
}

.left{
	display: flex;
	justify-content: center;
	margin: 2px 13px 0 0;
}
.right{
	display: flex;
	justify-content: center;
	margin: 3px 0 0 13px;
}

